import { useState } from 'react';
import { useObserver } from 'mobx-react';
import { useTodoStore } from './TodosContext';
import { Todo } from './Todo';

export const List = () => {

    const todosStore = useTodoStore();
    const [todoText, setTodoText] = useState("");
    const [showStore, setShowStore] = useState();

    const mouseLeave = () => setShowStore(false);
    const onClick = () => setShowStore(true);
    const onSubmit = (e) => {
        e.preventDefault();
        todosStore.addTodo(todoText);
        setTodoText("");
    };

    return useObserver( () => (
            <div>
                {todosStore.todos.map(todo => (
                    <Todo key={todo.id} todo={todo}  />
                ))}
                <form onSubmit={onSubmit} onMouseLeave={mouseLeave}>
                    <input
                        style={{display: showStore ? 'block' : 'none' }}
                        type="text"
                        name="title"
                        placeholder=" Type your ToDo and press Enter..."
                        required
                        minLength="1"
                        maxLength="50"
                        className="field"
                        value={todoText}
                        onChange={(e) => setTodoText(e.target.value)}/>
                    <div className="toggle" onClick={onClick}></div>
                </form>
            </div>
      ));
};