import { useObserver } from 'mobx-react';
import { useTodoStore } from './TodosContext';

export const Todo = (props) => {

    const todosStore = useTodoStore();

    const colorArray = [
        '#D33930',
        '#D53F31', 
        '#D74C32', 
        '#D95B34', 
        '#DB6C37', 
        '#DD7C3A',
        '#DF8D3D', 
        '#E29D41', 
        '#E5AC44',
        '#E8BC48'];

    const getStyle = () => {
        let i = todosStore.todos.indexOf(props.todo);
        return {
        color: props.todo.completed ? '#777' : '#D5D5D6',
        backgroundColor: props.todo.completed ? '#333' : colorArray[i],
        textDecoration: props.todo.completed ? 'line-through' : 'none',
        }
    }

    return useObserver( () => (
        <>
                <div className="todo" style={getStyle()} >
                    <input className="check" type="checkbox" value={props.todo.completed} onChange={() => todosStore.toggleTodo(props.todo.id)} />
                    <input className="todoText" type="text" value={props.todo.text} onChange={(e) => todosStore.editTodo(props.todo.id, e)} />
                    <button className="delete" title="Delete" onClick={() => todosStore.removeTodo(props.todo.id)}>&times;</button>
                </div>
        </>
    ));
};