import React from 'react';
import { createTodosStore } from './todosStore';
import { useLocalStore } from 'mobx-react';

const TodosContext = React.createContext(null);

export const TodosProvider = ({children}) => {

    const todosStore = useLocalStore(createTodosStore);

    return <TodosContext.Provider value={todosStore}>
                   {children}
               </TodosContext.Provider>;

};

export const useTodoStore = () => React.useContext(TodosContext);