import {nanoid} from 'nanoid';
import List from './common/list';

export function createTodosStore() {

    return {
        todos: List,
        addTodo(text) {
            this.todos.push({
                id: nanoid(), text, completed: false
            })
        },
        editTodo(id, e) {
            this.todos = this.todos.map(todo => {
                let editedText = e.target.value;
                if(todo.id === id) {
                  todo.text = editedText;
                }
                return todo;
                })
        },
        removeTodo(id) {
            this.todos = this.todos.filter(todo => todo.id !== id)
        },
        toggleTodo(id) {
            this.todos = this.todos.map(todo => {
                if(todo.id === id) {
                  todo.completed = !todo.completed;
                }
                return todo;
                })
        }
    }
};