import { HashRouter, Route, Switch } from 'react-router-dom';
import { List } from './List';
import './App.css';

function App() {

  return <>
    <HashRouter>
      <Switch>
        <Route exact path="/" component={App}>
          <>
            <List />
          </>
        </Route>
      </Switch>
    </HashRouter>
  </>
}

export default App;